export default {
  name: "RightToolbar",
  data: function data() {
    return {};
  },
  props: {
    showSearch: {
      type: Boolean,
      default: true
    }
  },
  methods: {
    //搜索
    toggleSearch: function toggleSearch() {
      this.$emit("update:showSearch", !this.showSearch);
    },
    //刷新
    refresh: function refresh() {
      this.$emit("queryTable");
    }
  }
};